.form-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: auto;

    .concert-input-form {
        font-family: "Spline Sans Mono", monospace;
        text-transform: uppercase;

        border-radius: 8px;
        border: 1px solid black;
        box-sizing: border-box;
        display: flex;
        justify-content: start;
        flex-direction: column;
        gap: 12px;
        padding: 12px;
        width: 90vw;

        label,
        .react-aria-Label {
            display: flex;
            justify-content: start;
            font-size: 12px;
            padding-bottom: 2px;
            padding-left: 12px;
        }

        input {
            height: 24px;
            font-family: "Spline Sans Mono", monospace;
            font-size: 14px;
            text-transform: uppercase;
        }

        .react-aria-DateField,
        .react-aria-TextField,
        .react-aria-TimeField {
            align-items: start;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: start;
            margin: 0;
            padding: 0;
            width: 100%;

            .react-aria-DateInput {
                border-radius: 6px;
                display: flex;
                font-size: 14px;
                min-width: 150px;
                padding: 4px;
                width: fit-content;
                white-space: nowrap;
            }

            .react-aria-Input,
            input {
                border-radius: 8px;
                padding-left: 8px;
                text-transform: uppercase;
                width: -webkit-fill-available;
            }
        }

        .event-info {
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            .react-aria-DateField,
            .react-aria-TimeField {
                width: fit-content;
            }
        }
    }

    .button-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 12px;
        padding-top: 10px;

        .react-aria-Button {
            font-family: "Spline Sans Mono", monospace;
            text-transform: uppercase;

            background-color: #FFBBA6;
            border-radius: 16px;
            border: none;
            box-shadow: none;
            width: 100%;


            appearance: none;
            vertical-align: middle;
            text-align: center;
            margin: 0;
            outline: none;
            padding: 8px 16px;
            text-decoration: none;

            &[data-pressed] {
                box-shadow: inset 0 1px 2px rgb(0 0 0 / 0.1);
                background: whitesmoke;
            }
        }
    }
}